import classes from "./CardItem.module.css";
import { useState } from "react";
import { useDispatch } from "react-redux";
import { trelloActions } from "../../store/reducers/trelloSlice";
import { TodoList } from "./TodoList";
import { Button, Input } from "@mui/material";
import { DeleteOutlined } from "@mui/icons-material";
export const CardItem = (props) => {
  const [value, setValue] = useState("");
  const dispatch = useDispatch();
  const cardItem = {
    completed: false,
    mainId: props.id,
    id: Math.random().toString() * 120,
    cardItem: value,
  };
  const addCardItem = (e) => {
    e.preventDefault();
    if (value.trim().length !== 0) {
      dispatch(trelloActions.addCardItem(cardItem));
      setValue("");
    }
  };
  const deleteCard = () => {
    dispatch(trelloActions.deleteCard(props.id));
  };
  return (
    <li className={classes.card_item}>
      <div className={classes.card_item_top}>
        <h2>{props.card}</h2>
        <Button
          startIcon={<DeleteOutlined />}
          color={"error"}
          variant={"outlined"}
          onClick={deleteCard}
        >
          Delete
        </Button>
      </div>
      <form onSubmit={addCardItem} className={classes.card_item_form}>
        <Input
          placeholder={"Add task"}
          value={value}
          onChange={(event) => setValue(event.target.value)}
          type="text"
        />
        <Button variant={"contained"} type="submit">
          Add task
        </Button>
      </form>
      <TodoList {...props} />
    </li>
  );
};
