import { useState } from "react";
import { useDispatch } from "react-redux";
import { trelloActions } from "../../store/reducers/trelloSlice";
import classes from "./TrelloForm.module.css";
import logo from "../../utils/image/trello.svg";
import Input from "@mui/material/Input";
import { Button } from "@mui/material";

export const TrelloForm = () => {
  const [value, setValue] = useState("");
  const dispatch = useDispatch();
  const card = {
    id: Math.random().toString() * 100,
    card: value,
    cardItem: [],
  };
  const onSubmitHandler = (e) => {
    e.preventDefault();
    if (value.trim().length !== 0) {
      dispatch(trelloActions.addCard(card));
      setValue("");
    }
  };
  return (
    <div className={classes.trello_form}>
      <img className="logo" src={logo} alt="" />
      <form onSubmit={onSubmitHandler}>
        <Input
          value={value}
          onChange={(event) => setValue(event.target.value)}
          placeholder={"Add Card"}
        />
        <Button type="submit" variant={"contained"}>
          Add card
        </Button>
      </form>
    </div>
  );
};
