import { useDispatch } from "react-redux";
import { trelloActions } from "../../store/reducers/trelloSlice";
import classes from "./TodoItem.module.css";
import { Button, Checkbox } from "@mui/material";
import { DeleteOutlined, DoneAll, RemoveDone } from "@mui/icons-material";
export const TodoItem = (props) => {
  const dispatch = useDispatch();
  const deleteTask = () => {
    dispatch(trelloActions.deleteCardItem(props));
  };
  const onChangeHandler = () => {
    dispatch(trelloActions.completed(props));
  };
  return (
    <li className={classes.todo_item}>
      <p
        style={
          props.completed
            ? {
                textDecoration: "line-through",
                color: "grey",
              }
            : {}
        }
      >
        {props.cardItem}
      </p>
      <Checkbox
        onChange={onChangeHandler}
        checked={props.cardItem.completed}
        icon={<DoneAll />}
        checkedIcon={<RemoveDone />}
      />
      <Button
        startIcon={<DeleteOutlined />}
        color={"error"}
        variant={"outlined"}
        onClick={deleteTask}
      >
        Delete
      </Button>
    </li>
  );
};
